import React, {
  KeyboardEvent,
  ReactNode,
  MouseEvent,
  useCallback,
  useState,
} from "react";
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { pink, teal } from '@mui/material/colors';
import { navigate, Link } from "gatsby";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import useScrollTrigger from "@mui/material/useScrollTrigger";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";
import Box from "@mui/material/Box";
import Slide from "@mui/material/Slide";
import IconButton from "@mui/material/IconButton";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

const theme = createTheme({
  palette: {
    primary: teal,
    secondary: pink,
  },
});

interface LayoutProps {
  children: ReactNode;
  backLink?: string;
  headerTitle?: string;
  shouldGoBack?: boolean;
}

interface Props {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window?: () => Window;
  children: React.ReactElement;
}

function HideOnScroll(props: Props) {
  const { children, window } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
  });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

const Layout: React.FC<LayoutProps> = ({ 
  backLink, 
  children, 
  shouldGoBack = false,
  headerTitle = 'KalviApp - TNEA' }) => {
  const [isOpenSideSheet, setIsOpenSideSheet] = useState(false);

  const toggleSideSheet = useCallback((event: KeyboardEvent | MouseEvent) => {
    if (
      event?.type === "keydown" &&
      ((event as React.KeyboardEvent).key === "Tab" ||
        (event as React.KeyboardEvent).key === "Shift")
    ) {
      return;
    }

    setIsOpenSideSheet((prevState) => !prevState);
  }, []);

  const openSideSheet = useCallback(() => {
    setIsOpenSideSheet(true);
  }, []);

  const goBack = useCallback(() => {
    navigate(-1);
  }, []);

  return (
    <ThemeProvider theme={theme}>
    <Container maxWidth="md" disableGutters>
      <HideOnScroll>
        <AppBar>
          <Toolbar>
            {backLink ? (
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                sx={{ mr: 2 }}
                component={Link}
                to={backLink}
              >
                <ArrowBackIcon />
              </IconButton>
            ) : shouldGoBack ? (
              <IconButton
                size="large"
                edge="start"
                color="inherit"
                aria-label="menu"
                sx={{ mr: 2 }}
                onClick={goBack}
              >
                <ArrowBackIcon />
              </IconButton>
            ) : (
              <></>
              // <IconButton
              //   size="large"
              //   edge="start"
              //   color="inherit"
              //   aria-label="menu"
              //   sx={{ mr: 2 }}
              //   onClick={toggleSideSheet}
              // >
              //   <MenuIcon />
              // </IconButton>
            )}
            <Typography variant="h6" display="inline" noWrap>
              {headerTitle}
            </Typography>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar />
      <SwipeableDrawer
        anchor={"left"}
        open={isOpenSideSheet}
        onClose={toggleSideSheet}
        onOpen={openSideSheet}
      >
        <Box
          role="presentation"
          sx={{ width: 300 }}
          onClick={toggleSideSheet}
          onKeyDown={toggleSideSheet}
        >
          KalviApp - TNEA
        </Box>
      </SwipeableDrawer>
      <Box>{children}</Box>
    </Container>
    </ThemeProvider>
  );
};
export default Layout;

